/* ============ desktop view ============ */
@media all and (min-width: 1024px) {
	.megasubmenu { 
		left:100%; top:0; min-height: 100%; min-width:500px;
	}
  .megasubmenu2 { 
		left:100%; top:0; min-height: 100%; min-width:300px;
	}
	
	.dropdown-menu > li:hover .megasubmenu{
		display: block;
	}
  .dropdown-menu > li:hover .megasubmenu2 {
		display: block;
	}
}	
/* ============ desktop view .end// ============ */
.screen-darken{
    content: ''; 
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0,0,0,.5);
    pointer-events: none;
    z-index:10; opacity:1; 
    visibility:visible;
    position: fixed;
  }
  
